<script>

    import ljCollapse from '../../../../components/LjCollapse'
    import {apiCaseCreateApproval} from "../../../../api/case";

    export default {
    name: 'info',
    props: ['form'],
    components: {
        ljCollapse
    },
    data(){
        return {
            stepNum:-1,
            value:'',
            formData:{},
            modulesShow: {
                one: true,
                two: false,
                three: false,
                four: false,
                five: false,
                six: true,
                seven: false,
                eight: false,
                nine: false,
                ten: false,
            }
        }
    },
    methods:{
        toNext(){
            let json = {
                "nmId":this.form.normalInfo.nmId,
                "caseStatus":"caseRegInfo"
            };
            apiCaseCreateApproval(json).then(res=>{

            })
        }
    }
}
