<template>
    <div class="dispose-page">
        <i-tabs></i-tabs>
        <div class="dispose-content">
            <el-tabs class="tab-header" type="border-card" v-model="activeName" tabPosition="top" @tab-click="handleClick">
                <el-tab-pane label="案件信息" name="first">
                    <div class="item-block">
                        <case-info :form="formData"/>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="立案审批" name="second">
                    <div class="item-block">
                        <case-reg-approval :caseDetail="formData"/>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="案情调查" name="third">
                    <div class="item-block">
                        <case-info-investigate :caseDetail="formData"/>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="案调终结" name="fourth">
                    <div class="item-block">
                        <case-investigate-over-app :caseDetail="formData"/>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="处罚告知" name="fifth">
                    <div class="item-block">
                        <case-inform :caseDetail="formData"/>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="处罚决定" name="6">
                    <div class="item-block">
                        <case-administrative-sanction :caseDetail="formData"/>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="案件执行" name="7">
                    <div class="item-block">
                        <case-punish-execute :caseDetail="formData"/>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="结案" name="8">
                    <div class="item-block">
                        <close-case :caseDetail="formData"/>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="归档" name="9">
                    <div class="item-block">
                        <place-file :caseDetail="formData"/>
                    </div>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>

<script>
    import iTabs from '../../../components/home/component/tabs'
    import caseInfo from './modules/info'
    import caseRegApproval from './modules/caseRegApproval'
    import caseInfoInvestigate from './modules/caseInfoinvestigate'
    import caseInvestigateOverApp from './modules/caseinvestigateOverApp'
    import caseInform from './modules/caseInform'

    import caseAdministrativeSanction from './modules/caseAdministrativeSanction'
    import casePunishExecute from './modules/casePunishExecute'
    import closeCase from './modules/closeCase'
    import placeFile from './modules/placeFile'
    import {apiNormalCaseDetailGet} from '../../../api/case'
    export default {
    name: 'index',
    components: {
        iTabs,
        caseInfo,
        caseRegApproval,
        caseInfoInvestigate,
        caseInvestigateOverApp,
        caseInform,
        caseAdministrativeSanction,
        casePunishExecute,
        closeCase,
        placeFile
    },
    data(){
      return {
          formData:{},
          stepNum: 0,
          active: 0,
          activeName: 'first',
          value: '',
          infoActiveNames:[],
          id:'',
      }
    },
    methods: {
        handleClick(){

        },
        initPage(){
            let id = this.$route.params.id;
            apiNormalCaseDetailGet(id).then(res => {
                this.formData = res;
                if (res.normalInfo.caseStatus == 'caseInfoinvestigate') {
                    this.activeName = 'third'
                }
            })
        }
    },
    created(){
        this.initPage()
    }
}
</script>

<style scoped lang="less">
    .dispose-page{
        padding: 10px;
        background: #f8f8f8 !important;

        .tab-header{
            background: transparent;
            border: none;
            box-shadow: none;
            /deep/.el-tabs__header{
                border-color: #cccccc;
                background-color: transparent;
                border-left: 1px solid #cccccc;
            }
            /deep/.el-tabs__header .el-tabs__item.is-active{
                border-color: transparent;
                border-top: 2px solid #409EFF;
                border-left: none;
                top: 2px;
                background-color: white;
                color: #409EFF;
            }
            /deep/.el-tabs__item.is-top{
                background-color: #f0f0f2;
                min-width: 110px;
                text-align: center;
                border-color: #cccccc;
                color: #333333;
            }
            /deep/.el-tabs__content{
                border: 1px solid #cccccc;
                border-top: none;
                background-color: white;
            }
        }
        .dispose-content{
            position: relative;
            top: -5px;
            padding: 0 10px;
            height: calc(~'100vh - 150px');
            overflow: auto;
            .item-block{
            }
        }
        /deep/.el-col .el-col-12{
            border: 1px solid #E4E4E4;
        }
    }
</style>
